import { Error404 } from '@components/errors/404';
import React from 'react';

interface IErrorProps {
  statusCode: number;
}

class Error extends React.Component<IErrorProps> {
  public static getInitialProps(ctx: any) {
    const statusCode = ctx.res ? ctx.res.statusCode : ctx.err ? ctx.err.statusCode : null;
    return { statusCode };
  }

  public render() {
    return this.props.statusCode ? (
      this.mapToPage(this.props.statusCode)
    ) : (
      <p>An error occurred on client'</p>
    );
  }

  private mapToPage(code: number) {
    switch (code) {
      case 404:
        return <Error404 />;
      default:
        return `An error ${this.props.statusCode} occurred on server`;
    }
  }
}

export default Error;
